@import url(https://fonts.googleapis.com/css?family=Space+Mono);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.rectangle {
    text-decoration: none;
	display: block;
	align-items: center;
	margin: auto;
    position: relative;
    margin-bottom: 25px;
	width: 50px;
    height: 50px;
	background: #0693E3;
	-webkit-transform: scale(0);
	        transform: scale(0);
	border-radius: 50%;
	color: white;
	opacity: 0;
	overflow: hidden;
	-webkit-animation: scale-in .3s ease-out forwards,
		expand .35s .25s ease-out forwards;
	        animation: scale-in .3s ease-out forwards,
		expand .35s .25s ease-out forwards;
}

.notification-text {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    font: inherit;
    -webkit-animation: fade-in .65s ease-in forwards;
            animation: fade-in .65s ease-in forwards;
    /* font-size: 120%; */
}

@-webkit-keyframes scale-in {
	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
		opacity: 1;
	}
}

@keyframes scale-in {
	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
		opacity: 1;
	}
}

@-webkit-keyframes expand {
	50% {
		width: 350px;
		border-radius: 6px;
	}
	100% {
		width: 300px;
		border-radius: 10px;
	}
}

@keyframes expand {
	50% {
		width: 350px;
		border-radius: 6px;
	}
	100% {
		width: 300px;
		border-radius: 10px;
	}
}

@-webkit-keyframes fade-in {
	0% {
		opacity: 0;
	}
	100% {
		opacity: .8;
	}
}

@keyframes fade-in {
	0% {
		opacity: 0;
	}
	100% {
		opacity: .8;
	}
}

@media screen and (max-width: 2000px) {
    .notification-text {
        font-size: 70%;
    }
}

@media screen and (max-width: 1300px) {
    .notification-text {
        font-size: 80%;
    }
}

@media screen and (max-width: 800px) {
    .notification-text {
        font-size: 95%;
    }
}





.loading{
    max-width: 400px;
    position: relative;
    margin: auto;
    padding-bottom: 10px;
}

.progress{
    width: 100%;
    height: 28px;
    border-radius: 2px;
    background: #20222A;
    margin: 0;
    /* border: 1px solid #1A1C21; */
    border-radius: 10px;
    box-shadow: inset 0 0 3px #222, 0 1px 0 #484848;
}

.progress_bar{
    width: 0%;
    height: 100%;
    border-radius: 10px;
    background: rgb(54,168,233); /* Old browsers */ /* FF3.6+ */
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,rgba(54,168,233,1)), color-stop(100%,rgba(17,126,210,1))); /* Chrome,Safari4+ */
    background: -webkit-linear-gradient(top, rgba(54,168,233,1) 0%,rgba(17,126,210,1) 100%); /* Chrome10+,Safari5.1+ */ /* Opera 11.10+ */ /* IE10+ */
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(54,168,233,1)),to(rgba(17,126,210,1)));
    background: linear-gradient(to bottom, rgba(54,168,233,1) 0%,rgba(17,126,210,1) 100%); /* W3C */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#36a8e9', endColorstr='#117ed2',GradientType=0 ); /* IE6-9 */
    padding: 0;
    margin: 0;    
}

#skill70 {
    -webkit-animation-name: skillbar70; /* Safari 4.0 - 8.0 */
    -webkit-animation-duration: 3s; /* Safari 4.0 - 8.0 */
    -webkit-animation-iteration-count: 1; /* Safari 4.0 - 8.0 */
    -webkit-animation-fill-mode: forwards; /* Safari 4.0 - 8.0 */
    animation-name: skillbar70;
    animation-duration: 3s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

#skill80 {
    -webkit-animation-name: skillbar80; /* Safari 4.0 - 8.0 */
    -webkit-animation-duration: 3s; /* Safari 4.0 - 8.0 */
    -webkit-animation-iteration-count: 1; /* Safari 4.0 - 8.0 */
    -webkit-animation-fill-mode: forwards; /* Safari 4.0 - 8.0 */
    animation-name: skillbar80;
    animation-duration: 3s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

#skill90 {
    -webkit-animation-name: skillbar90; /* Safari 4.0 - 8.0 */
    -webkit-animation-duration: 3s; /* Safari 4.0 - 8.0 */
    -webkit-animation-iteration-count: 1; /* Safari 4.0 - 8.0 */
    -webkit-animation-fill-mode: forwards; /* Safari 4.0 - 8.0 */
    animation-name: skillbar90;
    animation-duration: 3s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

@-webkit-keyframes skillbar70 {
    0% { width: 0% }
    100% { width: 70% }
}

@keyframes skillbar70 {
    0% { width: 0% }
    100% { width: 70% }
}

@-webkit-keyframes skillbar80 {
    0% { width: 0% }
    100% { width: 80% }
}

@keyframes skillbar80 {
    0% { width: 0% }
    100% { width: 80% }
}

@-webkit-keyframes skillbar90 {
    0% { width: 0% }
    100% { width: 90% }
}

@keyframes skillbar90 {
    0% { width: 0% }
    100% { width: 90% }
}

